@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
body{
	background-image: url(/randomCards/static/media/post03.eb5cc231.jpg);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-color: black;
	font-weight: 400;
	font-family: 'Fira Sans', sans-serif; /*	height: 100vh;*/
  }


.txtTitle{
	font-family: 'Luckiest Guy', cursive;
	font-weight: bolder;
	font-size: 150px !important;
	-webkit-text-stroke-width: 0.1px;
	-webkit-text-stroke-color: #fff;
	margin-top: auto;
	color:#008080;
	text-align: center;
}


.card {
	/* Add shadows to create the "card" effect */
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
	transition: 0.3s;
	background: rgba(236, 236, 234, 0.452);
	text-align: center;
	padding-top: 10px;
	margin-right: 25px ;
	margin-top: 10px;
	justify-content: center;

	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
	transition: 0.3s;
	border-radius: 10px; /* 5px rounded corners */
  }
  
  /* On mouse-over, add a deeper shadow */
  .card:hover {
	box-shadow: 0 18px 26px 0 rgba(73, 69, 69, 0.486);
	-webkit-filter: saturate(210%);
	        filter: saturate(210%);
  }
  
  /* Add some padding inside the card container */
  .container {
	padding-top: 0px;
	color: rgb(255, 255, 255);
	font-size: 25px;
	-webkit-text-stroke-width: 2.5px;
	-webkit-text-stroke-color: rgb(49, 10, 10);
  }

  h2{
	margin-top: 0px;
	padding-top: 5px;
	padding-bottom: 5px;
  }

  /* Add rounded corners to the top left and the top right corner of the image */
  
.imgCards {
	border-radius: 35px 35px 35px 35px;
	width: 300px;
	height: 300px;
	padding: 20px;
}


  .box {
	flex-direction: row;
	flex-wrap: wrap !important;
	display: flex;
	align-items: center;
	justify-content: center;
  }
  
  .btn{
	margin-top: 120px;
	background: #069e7d;
	color: rgb(255, 239, 239) !important;
	width: 160px;
	height: 160px;
	border-radius: 50%;
	border-width: 10px;
	border-style: none;

	font-size: 28px;
	font-weight: 900;
	
	font-weight: 900;
	font-family: 'Fira Sans', sans-serif; /*	height: 100vh;*/
	box-shadow: 0px 2px 2px rgba(133, 132, 132, 0.596);
	border-bottom: solid 8px #ff7c30;
	border-color: rgba(224, 219, 219, 0.781) !important;

  }
  
  .btn:active{
    transform: translateY(1px);
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.35);
    border-bottom: none;
}


  .btn:hover{
	  background: #03795f;
	  color: white;
  }
  

@media screen and (max-width:1850px){
	.txtTitle{
		font-size: 130px !important;
	}

	.container {
		font-size: 20px;
	}

	.imgCards {
		width: 200px;
		height: 200px;
	}
}

@media screen and (max-width:1380px){
	.txtTitle{
		font-size: 110px !important;
	}

	.container {
		font-size: 18px;
	}

	.imgCards {
		width: 150px;
		height: 150px;
	}
}

@media screen and (max-width:1120px){
	.container {
		font-size: 20px;
	}

	.card {
		background-color: #f5b5ae;
	}

	.imgCards {
		width: 250px;
		height: 250px;
	}
	.box {
		flex-direction: column;
		display: auto;
		align-items: center;
		justify-content: center;
	  }
}

@media screen and (max-width:600px){
	.txtTitle{
		font-size: 70px !important;
	}
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

